import React from 'react';
import Helmet from 'react-helmet';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { Link } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useNotistack, useRouter } from 'shared/hooks';
import { TemplateFormElements } from '../../components/TemplateFormElements';

export function CreateTemplate() {
	const { companyId } = useAppState();
	const { history } = useRouter();
	const { showNotification } = useNotistack();

	return (
		<div className="news-page py-4">
			<Helmet>
				<title>KENNEKT | Create Template</title>
				<meta name="description" content="KENNEKT Announcement" />
			</Helmet>
			<h1 className="mb-2 text-2xl font-bold dark:text-white">Create Template</h1>
			<div className="mb-4">
				<Breadcrumbs aria-label="breadcrumb">
					<Link to={`/news/templates/`}>
						<span className="text-sm text-primary transition hover:text-primary-dark">
							Templates List
						</span>
					</Link>
					<Typography color="textPrimary" className="!text-sm">
						Create Template
					</Typography>
				</Breadcrumbs>
			</div>
			<div className="uni-page__content">
				<EntityForm.Main
					entity="templates"
					name="AllTemplates"
					url={`/${companyId}/notification-templates/`}
					sendAsFormData={false}
					method="post"
					fields={[
						{
							name: 'subject',
							required: true,
							onSubmitKey: ''
						},
						{
							name: 'title',
							required: true,
							onSubmitKey: ''
						},
						{
							name: 'body',
							required: true,
							value: ' ',
							onSubmitKey: ''
						}
					]}
					onSuccess={() => {
						history.push('/news/templates');
					}}
					onError={(error): any => {
						const errorText = error?.response?.data?.message || 'Something went wrong';
						showNotification({ message: errorText, variant: 'error' });
					}}
				>
					{({ isSubmitting, values }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('create template values', values);
						}
						return <TemplateFormElements isSubmitting={isSubmitting} />;
					}}
				</EntityForm.Main>
			</div>
		</div>
	);
}
